.nav{
    z-index: 1;
}
.nav__menu{
    background-color: var(--container-color);
    position: fixed;
    inset-block: 0;
    left: -100%;
    width: 100%;
    padding: 60px 30px;
    transition: all 0.5s cubic-bezier(0.77, 0, 0.175, 1);
    z-index: 100;
}

.show-menu{
    left: 0;
}

.nav__link{
    display: flex;
    align-items: center;
    column-gap: 24px;
    border-bottom: 1px solid var(--border-color);
    padding-block: 14px;
}

.nav__icon, .nav__name{
    color: var(--title-color);
    font-size: 24px;
}

.nav__name{
    font-weight: var(--weight-500);
}

.active-nav .nav__icon, .active-nav .nav__name{
    color: var(--first-color);
}

.nav_toogle{
    position: fixed;
    top: 30px;
    right: 30px;
    width: 40px;
    height: 40px;
    border-radius: 5px;
    background-color: var(--container-color);
    padding: 12px 10px;
    z-index: 100;
}

.nav_toogle span{
    display: block;
    width: 20px;
    height: 2px;
    border-radius: 2px;
    background-color: var(--title-color);
    margin-bottom: 5px;
}

.animate-toogle span:first-child{
    transform: rotate(45deg) translate(6px, 4px);
}

.animate-toogle span:nth-child(2){
    display: none;
}

.animate-toogle span:last-child{
    transform: rotate(-45deg) translate(1px, 1px);
}


@media screen and (min-width: 1024px) {
    .nav__menu{
        all: unset;
    }

    .nav_toogle{
        display: none;
    }

    .nav{
        position: fixed;
        inset-block: 0;
        right: 30px;
        display: flex;
        align-items: center;
        z-index: 100;
    }

    .nav_item{
        margin-block: 20px;
    }

    .nav__link{
        padding: 0;
        border-bottom: none;
        background-color: var(--container-color);
        width: 50px;
        height: 50px;
        border-radius: 50%;
        position: relative;
    }

    .nav__icon{
        font: 20px;
        margin-inline: auto;
    }

    .nav__name{
        position: absolute;
        top: 0;
        right: 0;
        z-index: -1;
        height: 100%;
        line-height: 50px;
        padding-inline: 30px 25px;
        border-radius: 30px;
        font-size: var(--small-font-size);
        background-color: var(--first-color);
        color: var(--text-white);
        opacity: 0;
        visibility: hidden;
        transition: all 0.3s ease-in;
    }

    .nav__link:hover .nav__name{
        right: 27px;
        color: var(--text-white);
        opacity: 1;
        visibility: visible;
        border-radius: 30px 0 0 30px;
    }

    .nav__link:hover, .active-nav{
        background-color: var(--first-color);
    }

    .nav__link:hover .nav__icon,.active-nav .nav__icon{
        color: var(--text-white);
    }

}